body {
	font-family: Arial, Helvetica, sans-serif;
}

.hex-wrapper {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.hex-header {
	font-size: 45px;
}

.hex-desc {
	font-size: 25px;
	margin: 10px 0 30px 0;
}

.hex-color {
	width: 40vw;
	height: 40vh;
}

.hex-btn-wrapper {
	display: flex;
	flex-direction: row;
	margin-top: 1rem;
}

.hex-btn {
	margin: 1em;
	padding: 15px;
	border: none;
	border-radius: 5px;
	background-color: navy;
	color: white;
	font-weight: 800;
}

.hex-btn:hover {
	background-color: rgb(12, 169, 231);
}

.hex-answer-wrapper {
	font-size: 2rem;
	margin-top: 1rem;
}

.hex-correct {
	color: green;
}

.hex-wrong {
	color: red;
}
